#animated-shadow-quote {
  display: inline-block;
  margin: 1em;
  max-width: 5em;
  position: relative;
}
#animated-shadow-quote div {
  -webkit-animation: shadows 2s linear infinite alternate;
          animation: shadows 2s linear infinite alternate;
  display: inline-block;
  margin: 0;
  padding: 0.09em;
}
#animated-shadow-quote div cite {
  display: block;
  font-style: italic;
  text-align: right;
}
#animated-shadow-quote div cite:before {
  content: "- ";
}

@-webkit-keyframes shadows {
  0% {
    box-shadow: 0 2px 4px -2px rgba(0, 0, 0, 0.25);
    -webkit-transform: scale(0.95);
            transform: scale(0.95);
  }
  100% {
    box-shadow: 0 0 4px 2px rgba(0, 0, 0, 0.25);
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes shadows {
  0% {
    box-shadow: 0 2px 4px -2px rgba(0, 0, 0, 0.25);
    -webkit-transform: scale(0.95);
            transform: scale(0.95);
  }
  100% {
